body {
    font-family: 'Poppins', sans-serif;
}

.view-toggle {
    margin-top: 10px;
    .material-icons {
        cursor: pointer;
        font-size: 24px;
        padding: 5px;
    }
}
#imageDisplay {
    &.grid-view {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        gap: 10px;
    }
    &.list-view {
        display: block;
    }
}

.image-container {
    position: relative;
    margin-bottom: 10px;
    img {
        width: 100%;
        height: auto;
    }
}
.image-info {
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 5px;
    font-size: 12px;
}

#imageModal {
    transition: opacity 0.25s ease;
    .modal-content {
        transition: transform 0.3s ease-out;
    }
    .modal-content {
        margin: 15% auto;
        padding: 20px;
        border: 1px solid #888;
        width: 80%;
        transform: translateY(-50px); /* Initial state */
        opacity: 0;
    }
    .modal-content {
        position: relative;
        padding-bottom: 40px; /* Adjust the padding to fit the caption */
    }
    &.show .modal-content {
        transform: translateY(0);
        opacity: 1;
    }
}

.material-icons {
    cursor: pointer;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    &.modal-navigation {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        color: #FFF; /* or any color you prefer */
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
    }
    &.modal-navigation.next {
        right: 10px;
    }
    &.modal-navigation.prev {
        left: 10px;
    }
    &:not(:last-child) {
        margin-right: 10px; /* Adjust as needed */
    }
}
/* Style for the caption */
.modal-caption {
    position: absolute;
    width: 100%;
    bottom: 10px;
    text-align: center;
    color: #545b62; 
    font-size: 16px;
    line-height: 1.6;
}
.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}
.modal-navigator {
    display: flex;
    justify-content: flex-end; /* Aligns items to the right */
    align-items: center; /* Keeps items vertically centered */
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px; /* Adjust as needed */
    background-color: #ddd;
    opacity: 73%;
}
.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1140px;
    margin: 0 auto;
    padding: 10px;
}

.navigation-menu {
    list-style: none;
    display: flex;
    align-items: center; /* This will vertically align the menu items */
    gap: 20px;
}

.navigation-menu li {
    position: relative;
}

.dropdown-menu {
    display: none;
    position: absolute;
    list-style: none;
    top: 100%;
}
.header-container img.logo {
    max-height: 120px;
    width: auto; /* This will maintain the aspect ratio */
}
.dropdown:hover .dropdown-menu {
    display: block;
}

footer {
    text-align: center;
    padding: 20px;
    max-width: 1140px;
    margin: 0 auto;
}

